import React from 'react';
import './ImageLinkForm.css';
const ImageLinkForm =({onInputChange,onButtonSubmit}) =>{
     return(
       <div>
           <p className='f3'>
             {'This Magic brain will detects face in your picture. Give it a try!!'}
           </p>
           <div className='center'>
               <div className='form center pa4 br3 shadow-5'>
                 <input className='f4 pa2 br2 w-70 center' placeholder="Enter image URL" type='text' onChange={onInputChange}></input>
                 <button 
                   className='w-30 br2 grow f4 link ph3 pv2 dib white bg-light-purple'
                   onClick={onButtonSubmit}>Detect</button>
               </div> 
           </div>
       </div>   
     );
} 
export default ImageLinkForm; 